.home {
    .homeContent {
        background: linear-gradient(115deg,$color-1 50%,transparent 100%) ;
        min-height: $minHeight;
        width: 100%;
        min-height: $minHeight;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;
        &:after {
            content: 'test';
            position: absolute;
            height: 100%;
            width: inherit;
            background: url('../../media/billGates.jpg') no-repeat center/cover;
            z-index: -1;
            filter: brightness(30%);
        }
        .content {
            position: absolute;
            top :20%;
            left: 13%;
            font-family: $font-1;
            h1{
                font-size: 2.5rem;
                padding: 1.5rem 0 7rem;
                color: $color-3;
                text-shadow: 1px 4px 1px $black;

            }
            h2 {
                font-size: 2.5rem;
                padding: 1.5rem 0 7rem;
                color: $color-3;
                text-shadow: 1px 4px 1px $black;

            }
            .pdf {
                a {
                    font-size: 1.5rem;
                background: $color-3;
                padding: 1rem 2rem  ;
                border-radius: $borderRadius;
                box-shadow: 0 5px 1px rgb(15, 15, 15);
                transition: 0.2s ease ;
                &:hover{
                    background: $color-4;
                }
                }
            }

        }
    }
}


